// let repairlists=[];
export default {
	data() {
		return {
			repairlist: ""
		}
	},
	onLoad(options) {
		
	},
	onShow:function(){
		let ts = this,openid=uni.getStorageSync("openid");
		console.log(ts.$consturl + "wx/api/getRepairList");
		uni.request({
			url: ts.$consturl + "wx/api/getRepairList", //
			data: {
				openid:openid,
				jobType:119
			},
			method: 'GET',
			// header: {
			//         'content-type': 'application/json' //自定义请求头信息
			//     },
			success: function(res) {
				let alist=res.data.data,sz=[];
				console.log(res);
				for (let s of alist) {
					// console.log(s.address);
					let aa={};
					aa.createTime=new Date(s.createTime).toLocaleDateString();
					aa.type=s.type;aa.remark=s.remark;
					let ad=JSON.parse(s.address);
					aa.address=ad.region+"-"+ad.street+"-"+ad.address;
					aa.status=s.status;
					sz.push(aa);
				}
				ts.repairlist=sz.reverse();
		
			},
			complete: function() {
			}
		});
		uni.showToast({
		    title: '此功能，待开放',
			icon:"loading",
		    duration: 5000
		});
	},
	methods: {
		detail: function(det) {
			console.log(det);
			// uni.navigateTo({
			// 	url: '../delegate/delegate?ind='+det
			// });
		},
		click: function(e) {
			uni.navigateTo({
				url: './repair'
			});
		},
		toproposal:function(){
			// uni.navigateTo({
			// 	url: '../proposal/proposalPage?ind=4'
			// });
		}
	}
}
